<template>
  <div class="column">
    <div class="column-header">{{ header }}</div>
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: 'Column',
  computed:{
    header(){
      return this.$attrs.header
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.column {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 5px;
  margin: 0.5rem;
  font-size: 1rem;
  color: #777;
}
.column-header {
  font-size: 1.15rem;
  color: black;
  margin-bottom: 0.75rem;
}

</style>



