<template>
  <div class="level-bar">
    <div class="info" >
      {{ label + ': ' + Number(level)*20  }} % proficiency
    </div>
    <svg viewBox="0 0 20 25" width="20" height="16" role="presentation" class="icon--inline">
      <use xmlns:xlink="http://www.w3.org/1999/xlink" v-bind:href="'/src/assets/bars.svg#'+icon"></use>
    </svg>

    <span>{{ label }}</span>
  </div>
</template>

<script>
export default {
  name: 'LevelBar',
  props: ['level', 'label'],

  computed:{
    icon(){
      const iconMap = {
        1: 'bar-1',
        2: 'bar-2',
        3: 'bar-3',
        4: 'bar-4',
        5: 'bar-5',
      }

      const level = Number(this.level);

      return iconMap[level]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
span {
  color: #656565;
}
.level-bar {
  margin: 1rem 0.25rem;
}
.info {
  display: hidden;
  visibility: hidden;
  position: absolute;
  padding: 2px;
  z-index: 5;
  background: #333;
  color: #fff;
  border-radius: 0.4rem;
  transition: transform 0.3s ease-in-out 0.9s;
  transform: scale(0,0);
}

.level-bar:hover .info{
  display: block;
  visibility: visible;


  margin-top: -2rem;
  transform: scale(1,1);
  animation-direction: forwards;

}
.level-bar:hover{
  margin: 1rem 0.25rem;
  color: orange;
}

</style>



