<template>
  <div class="experience-entry" >
    <span><div class="name">{{ name }}</div> <div class="dates"> {{ dates }}</div></span>

    <span class="company"><strong> {{ company }} </strong><span>{{ location }}</span></span>

    <div class="description">
    <slot></slot>
    </div >
  </div>
</template>

<script>
export default {
  name: 'ExperienceEntry',
  // props:['accentColor'],
  computed: {
    company(){
      return this.$attrs.company + ', '
    },
    location(){
      return this.$attrs.location
    },
    dates(){
      return this.$attrs.dates
    },
    name(){
      return this.$attrs.name
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.experience-entry {
  margin: 1rem;
  color: #636363;
  font-size: 1rem;
  padding-top: 0.5rem;
}
.name{
  float: left;
  font-size: 1.2rem;
}
.dates {
  float: right;
  font-size: 0.8rem;
  color: #999;
  text-transform: uppercase;
}
.company {
  clear: left;
  float: left;
  margin: 1rem 0;
  color: #454545;
}

.description {
  clear: left;
  text-align: left;
  text-justify: justify;
}

</style>



