<template>
  <div class="card" :class="accentStyle">
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: 'Card',
  // props:['accentColor'],
  computed: {
    accentStyle(){
      const color = this.$attrs['accent-color'];
      return `card__${color}-accent`
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.card {
  background: #fff;
  cursor: pointer;
  padding: 20px;
  margin: 1.4rem;
  border-radius: 0.43rem;
  box-shadow: 2px 2px 8px 0px rgba(40, 50, 60, 0.2);
  transition: box-shadow 0.35s ease;
}
.card:hover {
  box-shadow: 7px 10px 20px 1px rgba(40, 50, 60, 0.2);
}
.card__blue-accent {
  border-top: 0.3rem solid #7ad6fa;
}

.card__orange-accent {
  border-top: 0.3rem solid #fad67a;
}
.card__cyan-accent {
  border-top: 0.3rem solid #7afad6;
}

.card__green-accent {
  border-top: 0.3rem solid #7afaad;
}

.card__purple-accent {
  border-top: 0.3rem solid #ba4ada;
}
.card__gradient-accent {

  background-image: linear-gradient(-265deg,#7afaad, #ba4ada);
    /* background-position: 0 0, 0 100%; */
  background-repeat: no-repeat;
  background-size: 102% 0.3rem;


}
h1, h2 {
  font-size: 2.23;
  margin-top: 2.23;
  margin-bottom: 0.85rem;
  font-weight: 300;
  color: #3d3349;
}
h2 {
  font-size: 1.5rem;
  color: #6d6a6b;
}
h3 {
  font-size: 1.3rem;
  font-weight: 300;
  color: #575456;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



